.react-pdf__Document{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow-x: auto;
}
.react-pdf__Page{
    background-color: #ffffff;
    background-clip: border-box;
    border: 0.0625rem solid rgba(46, 54, 80, 0.125);
    border-radius: 0.5rem;
    box-shadow: 5px 5px 5px 5px #81787866;
    margin: 0 auto;
}